import { watch } from 'vue';
import { useThemeStore } from '../store/theme';

/**
 *  composable
 *  CSS 
 */
export function useTheme() {
  const themeStore = useThemeStore();

  //  CSS 
  watch(
    () => themeStore.effectiveColors,
    () => {
      themeStore.applyCssVariables();
    },
    { deep: true },
  );

  return {
    themeStore,
  };
}
